import React, { useState } from "react";
import { Button, Form, Input, message } from "antd";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { formItemLayout, tailFormItemLayout } from "../../Utilities/FormLayout";
import { positionSer } from "../../Services/positionService";
import { setDataPosition } from "../../Redux/actions/actionPosition";

export const AddPosition = () => {
  let dispatch = useDispatch();
  let navigate = useNavigate();
  const onFinishSign = (values) => {
    positionSer
      .postPosition(values)
      .then((res) => {
        console.log(res.data.content);
        dispatch(setDataPosition(res.data.content));
        message.success("Thm mi thnh cng");
        setTimeout(() => {
          navigate("/admin/position");
        }, 1500);
      })
      .catch((err) => {
        message.error("Tht bi xin kim tra li");
        console.log(err);
      });
  };
  const renderSign = () => {
    return (
      <Form
        {...formItemLayout}
        form={form}
        name="register"
        onFinish={onFinishSign}
        scrollToFirstError
      >
        <Form.Item
          name="tenViTri"
          label="Tn v tr"
          rules={[
            {
              required: true,
              message: "Khng b trng",
              whitespace: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="tinhThanh"
          label="Tn chi tit tnh thnh"
          rules={[
            {
              required: true,
              message: "Khng b trng",
              whitespace: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="quocGia"
          label="Tn quc gia"
          rules={[
            {
              required: true,
              message: "Khng b trng",
              whitespace: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          name="hinhAnh"
          label="Hnh nh khch sn (URL)"
          rules={[
            {
              required: true,
              message: "Khng b trng",
              whitespace: true,
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item {...tailFormItemLayout}>
          <Button className="mt-5" type="primary" htmlType="submit">
            ng K
          </Button>
        </Form.Item>
      </Form>
    );
  };
  const [form] = Form.useForm();
  return <>{renderSign()}</>;
};

export default AddPosition;
