import React, { useEffect,useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Button, Input } from "antd";
import { NavLink } from "react-router-dom";
import { searchRoom, setDataRoom } from "../../Redux/actions/actionRoom";
import RoomTable from "./RoomTable";
const { Search } = Input;
export default function Room() {
  const { dataRoom } = useSelector((state) => state.roomReducer);
  const dispatch = useDispatch();
  const [dataSearch, setDataSearch] = useState("");
  useEffect(() => {
    dispatch(setDataRoom());
  }, []);
  useEffect(() => {
    const timeout = setTimeout(() => {
      dispatch(searchRoom(dataSearch));
    }, 1000);
    return () => clearTimeout(timeout);
  }, [dataSearch]);
  const onSearch = (e) => {
    setDataSearch(e.target.value);
  };

  return (
    <div className="container mx-auto">
      <h1 className="text-2xl">Qun l Phng</h1>
      <div>
        <NavLink to="/admin/addRoom">
          <Button>Thm phng</Button>
        </NavLink>
      </div>
      <Search
        placeholder="Nhp id phng mun tm"
        allowClear
        onChange={onSearch}
        style={{
          width: 600,
        }}
        className="py-2"
      />
      <RoomTable dataRoom={dataRoom} />
    </div>
  );
}
