import React, { useState, useEffect } from "react";
import { Button, Checkbox, Form, Input, message, Col, Row } from "antd";
import { useDispatch } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { roomServ } from "../../Services/roomService";
import { formItemLayout, tailFormItemLayout } from "../../Utilities/FormLayout";
export const EditRoom = () => {
  let { id } = useParams();
  let navigate = useNavigate();
  const [checkBox, setCheckBox] = useState(null);
  const [form] = Form.useForm();
  useEffect(() => {
    roomServ.getDataRoom(id).then((res) => {
      let newData = [];

      for (const key in res.data.content) {
        const element = res.data.content[key];
        if (element === true) {
          newData.push(key);
        }
      }
      form.setFieldsValue({
        ...res.data.content,
      });
      setCheckBox(newData);
    });
  }, [id]);

  const [dataBox, setDataBox] = useState([
    "mayGiat",
    "tivi",
    "doXe",
    "hoBoi",
    "dieuHoa",
    "banLa",
    "banUi",
    "bep",
    "wifi",
  ]);
  const onFinishSign = (values) => {
    let newData = [];
    dataBox.forEach((item) => {
      newData[item] = true;
    });
    let dataSend = { ...values, ...newData };
    roomServ
      .editRoom(id, dataSend)
      .then((res) => {
        console.log(res.data.content);
        message.success("Update thnh cng");
        setTimeout(() => {
            navigate("/admin/room");
        }, 1500);
      })
      .catch((err) => {
        message.error("Tht bi xin kim tra li");
        console.log(err);
      });
  };
  const onChange = (checkedValues) => {
    setDataBox(checkedValues);
  };
  const renderSign = () => {
    return (
      <Form
        {...formItemLayout}
        form={form}
        name="register"
        onFinish={onFinishSign}
        scrollToFirstError
      >
        <Form.Item name="tenPhong" label="Tn phng">
          <Input />
        </Form.Item>
        <Form.Item name="khach" label="S khch">
          <Input type={"number"} />
        </Form.Item>
        <Form.Item name="phongNgu" label="S phng ng">
          <Input type={"number"} />
        </Form.Item>
        <Form.Item name="giuong" label="S ging">
          <Input type={"number"} />
        </Form.Item>
        <Form.Item name="phongTam" label="Phng tm">
          <Input type={"number"} />
        </Form.Item>
        <Form.Item name="giaTien" label="Gi tin 1 m">
          <Input type={"number"} />
        </Form.Item>
        <Form.Item name="maViTri" label="M v tr">
          <Input type={"number"} />
        </Form.Item>
        <Form.Item name="hinhAnh" label="Hnh nh khch sn (URL)">
          <Input />
        </Form.Item>
        <Form.Item name="moTa" label="M t v phng thu">
          <Input.TextArea rows={4} />
        </Form.Item>
        {checkBox && (
          <Checkbox.Group
            defaultValue={checkBox}
            style={{
              width: "100%",
            }}
            onChange={onChange}
          >
            <Row>
              <Col span={8}>
                <Checkbox value="mayGiat">My git</Checkbox>
              </Col>
              <Col span={8}>
                <Checkbox value="banLa">Bn l</Checkbox>
              </Col>
              <Col span={8}>
                <Checkbox value="banUi">Bn i</Checkbox>
              </Col>
              <Col span={8}>
                <Checkbox value="tivi">Ti vi</Checkbox>
              </Col>
              <Col span={8}>
                <Checkbox value="dieuHoa">iu ha</Checkbox>
              </Col>
              <Col span={8}>
                <Checkbox value="bep">Bp</Checkbox>
              </Col>
              <Col span={8}>
                <Checkbox value="doXe">Bi  xe</Checkbox>
              </Col>
              <Col span={8}>
                <Checkbox value="hoBoi">H bi</Checkbox>
              </Col>
              <Col span={8}>
                <Checkbox value="wifi">Wi fi</Checkbox>
              </Col>
            </Row>
          </Checkbox.Group>
        )}
        <Form.Item {...tailFormItemLayout}>
          <Button className="mt-5" type="primary" htmlType="submit">
            Cp nht
          </Button>
        </Form.Item>
      </Form>
    );
  };

  return <>{renderSign()}</>;
};

export default EditRoom;
